*{
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body{
    min-height: 100vh;
    overflow: hidden;
    background: linear-gradient(300deg ,#9966CC, rgb(255, 189, 255));
    background-repeat: no-repeat;
    font-family: sans-serif;
}
#login{
    max-width: 400px;
    margin: 80px auto 0px auto;
    padding: 10px 0px;

    border-radius: 8px;
    background: #fff;

    text-align: center;
}
@media screen and (max-width: 500px) {
.mobile{
      margin: 10px auto 0px auto!important;
}
}
#login h2{
    color: #777;
    margin: 0px 0px 40px;
    font-size: 40px;
}
#login .input-box{
    position: relative;
    margin: 15px 0px 10px 0px;
}
#login .input-box  i.icon{
    position: absolute;
    top: 50%;
    -webkit-transform: translate(40%,-60%);
            transform: translate(40%,-60%);
    color: #777;
}
#login .input-box input[type="text"],
  #login .input-box input[type="password"]{
    font-size: 16px;
    border: none;
    border-bottom: 2px solid #777;
    outline: none;
    padding-left: 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: bold;
    color: #777;
    -webkit-transition: .2s;
    transition: .2s;
}
#login .input-box input:focus,
  #login .input-box input:valid{
    border-bottom: 2px solid #d774e4;
}
#login .input-box input[type="submit"]{
    border: none;
}
#login .input-box label{
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
#login .input-box label i{
    cursor: pointer;
    color: #777;
    font-size: 20px;
}
#login-media{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 40px 0px 20px 0px;
}
#login-media div{
    width: 25%;
    height: 50px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
    font-size: 20px;
    color: rgb(143, 143, 143);
    -webkit-transition: .2s ease;
    transition: .2s ease;
}
#login-media div:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    font-size: 27px;
}
#login-media div:nth-child(1):hover{
    color: #3B5998;
}
#login-media div:nth-child(2):hover{
    color: #c2c2c2;
}
#login-media div:nth-child(3):hover{
    color: #00acee;
}
#login-media div:nth-child(4):hover{
    color: black;
}
.lock:hover{
    cursor: pointer;
}
p{
    font-size: 12px;
    font-style: italic;
    letter-spacing: 1px;
    color: rgb(255, 58, 44);
}
.input{
    margin: 10px;
}
main{
    max-width: 400px;
    margin: 80px auto 0px auto;

    border-radius: 8px;
    background: #fff;
}
.hide{
    overflow: hidden;
}
.router{
    display: block;
    width: 50%;
    float: left;

    color: rgb(189, 189, 189);
    text-decoration: none;
    text-align: center;
    padding: 5px;
    background: rgb(248, 248, 248);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 2px solid rgb(243, 243, 243);
    -webkit-transition: .3s ease;
    transition: .3s ease;
}
.all{
    border-radius: 8px 0 0 0;
    padding: 20px;
}
.all i{
    font-size: 20px;
}
.comp{
    border-radius: 0 8px 0 0;
    padding: 18.5px;
}
.comp i{
    font-size: 22px;
}
.active{
    color: #9966CC;
    background: #fff;
    border: 2px solid #fff;
}
.helpful{
    clear: both;
}
.add{
    display: block;
    text-decoration: none;
    text-align: center;
    font-weight: 900; 
    font-size: 40px;
    font-family: none;
    position: relative;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
    z-index: 1000;
    width: 50px;
    height: 50px;
    border: none;
    border-radius: 50%;
    background: rgb(255, 148, 105);
    color: #fff;
    outline: none;
}
.addClick{
    -webkit-animation: lol .5s ease;
            animation: lol .5s ease;
}
@-webkit-keyframes lol {
20%{
      -webkit-transform: translate(-50%, 50%) scale(0.8);
              transform: translate(-50%, 50%) scale(0.8);
}
60%{
      -webkit-transform: translate(-50%, 50%) scale(1.2);
              transform: translate(-50%, 50%) scale(1.2)
}
}
@keyframes lol {
20%{
      -webkit-transform: translate(-50%, 50%) scale(0.8);
              transform: translate(-50%, 50%) scale(0.8);
}
60%{
      -webkit-transform: translate(-50%, 50%) scale(1.2);
              transform: translate(-50%, 50%) scale(1.2)
}
}
.exit{
    display: block;
    position: absolute;
    width: 30px;
    height: 30px;
    background: rgb(223, 63, 63);
    border-radius: 50%;
    border: none;
    color: #fff;
    top: 0;
    right: 0;
    margin: 10px 10px;
}

#ToDoApp{
  min-height: 50px;
  max-height: 500px;
  overflow: auto;
  border-radius: 0 0 8px 8px;
  background: #fff;
}
.completed{
  opacity: 0.5;
  text-decoration: line-through;
}
.completed h2{
  text-decoration: line-through;
}


